import React from 'react';
import { ToggleButton } from './ToggleButton';
import styles from './NoteButton.module.css';

const NoteText = ({ children }) => {
  return <span>{children}</span>;
};

// Note glyphs are unicode, taken from http://www.unicode.org/charts/PDF/U1D100.pdf
export const NoteButton = ({
  noteType,
  onClick,
  current,
  isRest,
  className = '',
  id = '',
}) => {
  let glyph;
  switch (noteType) {
    case 'whole':
      glyph = isRest ? <NoteText>ùÑª</NoteText> : <NoteText>ùÖù</NoteText>;
      break;
    case 'half':
      glyph = isRest ? <NoteText>ùÑº</NoteText> : <NoteText>ùÖû</NoteText>;
      break;
    case 'quarter':
      glyph = isRest ? <NoteText>ùÑΩ</NoteText> : <NoteText>ùÖü</NoteText>;
      break;
    case 'eighth':
      glyph = isRest ? <NoteText>ùÑæ</NoteText> : <NoteText>ùÖ†</NoteText>;
      break;
    case '16th':
      glyph = isRest ? <NoteText>ùÑø</NoteText> : <NoteText>ùÖ°</NoteText>;
      break;
    case '32nd':
      glyph = isRest ? <NoteText>ùÖÄ</NoteText> : <NoteText>ùÖ¢</NoteText>;
      break;
    case '64th':
      glyph = isRest ? <NoteText>ùÖÅ</NoteText> : <NoteText>ùÖ£</NoteText>;
      break;
    case '128th':
      glyph = isRest ? <NoteText>ùÖÇ</NoteText> : <NoteText>ùÖ§</NoteText>;
      break;
  }
  return (
    <ToggleButton
      checked={current === noteType}
      onClick={onClick}
      className={className}
      id={`noteButton-${noteType}`}
    >
      <span className={styles.noteButton}>{glyph}</span>
    </ToggleButton>
  );
};
