import React from 'react';
import styles from './Button.module.css';

interface ButtonProps {
  children?: any;
  disabled?: boolean;
  className?: string;
  id?: string;
  onClick?: () => void;
}
export const Button = ({
  children,
  disabled,
  className,
  id,
  onClick,
}: ButtonProps) => {
  return (
    <button
      disabled={disabled}
      id={id}
      className={`${styles.button} ${className}`}
      onClick={onClick}
    >
      {children}
    </button>
  );
};
