import React from 'react';
import styles from '../ScoreEditor.module.css';

interface DropdownProps {
  children?: any;
  variant?: string;
  className?: string;
  id?: string;
  onChange?: (e: any) => void;
}
export const Dropdown = ({
  children,
  variant,
  className,
  id,
  onChange,
}: DropdownProps) => {
  return (
    <form className={styles.form} onChange={onChange}>
      <select id={id} className={className}>
        {children}
      </select>
    </form>
  );
};
