export const noteToVex = (note) => {
  const isNote = Notes.findIndex((n) => n.note === note);
  const isAlt = Notes.findIndex((n) => n.alt === note);
  if (isNote !== -1) {
    return VexNotes[isNote].note;
  }
  if (isAlt !== -1) {
    return VexNotes[isAlt].alt;
  }
};

