import React from 'react';
import styles from './ToggleButton.module.css';

interface ButtonProps {
  children?: any;
  className?: string;
  id?: string;
  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
  checked?: boolean;
  disabled?: boolean;
}
export const ToggleButton = ({
  children,
  className,
  id,
  checked,
  onClick,
  disabled,
}: ButtonProps) => {
  if (checked) {
    return (
      <button
        id={id}
        className={`${styles.toggleButton} ${styles.checked} ${className}`}
        onClick={onClick}
      >
        {children}
      </button>
    );
  }
  return (
    <button
      id={id}
      disabled={disabled}
      className={`${styles.toggleButton} ${className}`}
      onClick={onClick}
    >
      {children}
    </button>
  );
  // return (
  //   // <div>
  //   //   <input
  //   //     disabled={disabled}
  //   //     type={type}
  //   //     checked={checked}
  //   //     className={className}
  //   //     id={id}
  //   //     onChange={onChange}
  //   //     onClick={onClick}
  //   //     value={value}
  //   //   />
  //   //   <label htmlFor={id}>{children}</label>
  //   // </div>
  // );
};
